function _student_behavior_all_years(FileAttachment){return(
FileAttachment("student_behavior_all_years.csv").csv()
)}

function _filtered_students_night_sleep(student_behavior_all_years){return(
student_behavior_all_years.filter(x => (
  x['f_slp:fitbit_sleep_intraday_rapids_sumdurationasleepunifiedmain:night'] != "" && (x['f_screen:phone_screen_rapids_sumdurationunlock:allday'] != "") && (x['f_screen:phone_screen_rapids_sumdurationunlock:night'] != "") && (x['f_screen:phone_screen_rapids_sumdurationunlock:morning'] != "") &&
  (x['f_screen:phone_screen_rapids_sumdurationunlock:afternoon'] != "")
  && (x['f_screen:phone_screen_rapids_sumdurationunlock:evening'] != "")
))
)}

function _year_drop_down(Inputs){return(
Inputs.select(["2018", "2019", "2020", "2021"], {label: "Select a year"})
)}

function _filtered_students_year(filtered_students_night_sleep,year_drop_down){return(
filtered_students_night_sleep.filter(x => x.Year == year_drop_down)
)}

function _student_phone(vl,filtered_students_year)
{
  // Aggregating for each student over each week

  const brush = vl.selectInterval()
//    .encodings('y');

  const markPoint = vl.markPoint()
    .title('student phone usage all day over 12 week quarter')
    .params(brush)
    .encode(
       vl.x().fieldO('week_period'),
       vl.y().fieldQ('f_screen:phone_screen_rapids_sumdurationunlock:allday').title('student phone usage in minutes'),
    //  vl.color().value('green')
    ).height(250).width(650)
  
  const morning_circle_graph = vl.markCircle()
    .title('Morning Phone Usage')
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
      .aggregate(
        vl.average('f_slp:fitbit_sleep_intraday_rapids_sumdurationasleepunifiedmain:night').as('average_sleep_time'),
        vl.average('f_screen:phone_screen_rapids_sumdurationunlock:morning').as('average_morn_unlock_time')
      )
    ).encode(
      vl.x().fieldQ('average_morn_unlock_time').scale({domain: [0,360]}).title('average unlock time by student in minutes'),
      vl.y().fieldQ('average_sleep_time').scale({domain: [0,360]}).title('average sleep time by student in minutes'),
      vl.tooltip([{title: 'Student Identifier', field:'pid'}, {title: 'Average Unlock Time', field:'average_morn_unlock_time'}, {title: 'Average Sleep Time', field:'average_sleep_time'}]),
    //  vl.color().value('#FFFF00'),
      vl.stroke().value('#000000'),
      vl.strokeWidth().value(.2)
    );

  const morning_trend_line = vl.markLine()
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
         .aggregate(
            vl.average('f_slp:fitbit_sleep_intraday_rapids_sumdurationasleepunifiedmain:night').as('average_sleep_time'),
            vl.average('f_screen:phone_screen_rapids_sumdurationunlock:morning').as('average_morn_unlock_time')
        ),
      vl.regression('average_sleep_time').on('average_morn_unlock_time')
    ).encode(
      vl.y().fieldQ('average_sleep_time').scale({domain: [0, 360]}),
      vl.x().fieldQ('average_morn_unlock_time').scale({domain: [0, 360]}),
      vl.color().value('red')
    )

  const morning_layer = vl.layer(morning_circle_graph, morning_trend_line).width(300).height(200);



  const afternoon_circle_graph = vl.markCircle()
    .title('Afternoon Phone Usage')
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
      .aggregate(
        vl.average('f_slp:fitbit_sleep_intraday_rapids_sumdurationasleepunifiedmain:night').as('average_sleep_time'),
        vl.average('f_screen:phone_screen_rapids_sumdurationunlock:afternoon').as('average_after_unlock_time')
      )
    ).encode(
      vl.x().fieldQ('average_after_unlock_time').scale({domain: [0,360]}).title('average unlock time by student in minutes'),
      vl.y().fieldQ('average_sleep_time').scale({domain: [0,360]}).title('average sleep time by student in minutes'),
      vl.tooltip([{title: 'Student Identifier', field:'pid'}, {title: 'Average Unlock Time', field:'average_after_unlock_time'}, {title: 'Average Sleep Time', field:'average_sleep_time'}]),
     // vl.color().value('#86B7E0'),
      vl.stroke().value('#000000'),
      vl.strokeWidth().value(.2)
    );

  const afternoon_trend_line = vl.markLine()
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
         .aggregate(
            vl.average('f_slp:fitbit_sleep_intraday_rapids_sumdurationasleepunifiedmain:night').as('average_sleep_time'),
            vl.average('f_screen:phone_screen_rapids_sumdurationunlock:afternoon').as('average_after_unlock_time')
        ),
      vl.regression('average_sleep_time').on('average_after_unlock_time')
    ).encode(
      vl.y().fieldQ('average_sleep_time').scale({domain: [0, 360]}),
      vl.x().fieldQ('average_after_unlock_time').scale({domain: [0, 360]}),
      vl.color().value('red')
    )

  const afternoon_layer = vl.layer(afternoon_circle_graph, afternoon_trend_line).width(300).height(200);



  const evening_circle_graph = vl.markCircle()
    .title('Evening Phone Usage')
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
      .aggregate(
        vl.average('f_slp:fitbit_sleep_intraday_rapids_sumdurationasleepunifiedmain:night').as('average_sleep_time'),
        vl.average('f_screen:phone_screen_rapids_sumdurationunlock:evening').as('average_evening_unlock_time')
      )
    ).encode(
      vl.x().fieldQ('average_evening_unlock_time').scale({domain: [0,360]}).title('average unlock time by student in minutes'),
      vl.y().fieldQ('average_sleep_time').scale({domain: [0,360]}).title('average sleep time by student in minutes'),
      vl.tooltip([{title: 'Student Identifier', field:'pid'}, {title: 'Average Unlock Time', field:'average_evening_unlock_time'}, {title: 'Average Sleep Time', field:'average_sleep_time'}]),
      // vl.color().value('#BF77F6'),
      vl.stroke().value('#000000'),
      vl.strokeWidth().value(.2)
    );

  const evening_trend_line = vl.markLine()
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
         .aggregate(
            vl.average('f_slp:fitbit_sleep_intraday_rapids_sumdurationasleepunifiedmain:night').as('average_sleep_time'),
            vl.average('f_screen:phone_screen_rapids_sumdurationunlock:evening').as('average_evening_unlock_time')
        ),
      vl.regression('average_sleep_time').on('average_evening_unlock_time')
    ).encode(
      vl.y().fieldQ('average_sleep_time').scale({domain: [0, 360]}),
      vl.x().fieldQ('average_evening_unlock_time').scale({domain: [0, 360]}),
      vl.color().value('red')
    );

  const evening_layer = vl.layer(evening_circle_graph, evening_trend_line).width(300).height(200);


   const night_circle_graph = vl.markCircle()
    .title('Night Phone Usage')
    .transform(
      vl.filter(brush),
        // vl.filter('datum.pid == student_selector || student_selector == "ALL"'),
      vl.groupby('pid')
      .aggregate(
        vl.average('f_slp:fitbit_sleep_intraday_rapids_sumdurationasleepunifiedmain:night').as('average_sleep_time'),
        vl.average('f_screen:phone_screen_rapids_sumdurationunlock:night').as('average_night_unlock_time')
      )
    ).encode(
      vl.x().fieldQ('average_night_unlock_time').scale({domain: [0,360]}).title('average unlock time by student in minutes'),
      vl.y().fieldQ('average_sleep_time').scale({domain: [0,360]}).title('average sleep time by student in minutes'),
      vl.tooltip([{title: 'Student Identifier', field:'pid'}, {title: 'Average Unlock Time', field:'average_night_unlock_time'}, {title: 'Average Sleep Time', field:'average_sleep_time'}]),
    //  vl.color().value('#999999'),
      vl.stroke().value('#000000'),
      vl.strokeWidth().value(.2)
    )

  const night_trend_line = vl.markLine()
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
         .aggregate(
            vl.average('f_slp:fitbit_sleep_intraday_rapids_sumdurationasleepunifiedmain:night').as('average_sleep_time'),
            vl.average('f_screen:phone_screen_rapids_sumdurationunlock:night').as('average_night_unlock_time')
        ),
      vl.regression('average_sleep_time').on('average_night_unlock_time')
    ).encode(
      vl.y().fieldQ('average_sleep_time').scale({domain: [0, 360]}),
      vl.x().fieldQ('average_night_unlock_time').scale({domain: [0, 360]}),
      vl.color().value('red')
    );

  const night_layer = vl.layer(night_circle_graph, night_trend_line).width(300).height(200);


  const main_object = vl.vconcat(vl.hconcat(morning_layer, afternoon_layer), vl.hconcat(evening_layer, night_layer)).config({padding: '50'})
    .title({text: 'student average screen time during different time periods affect on average night sleep scores', fontSize: "15"});

  

  return vl.vconcat(markPoint, main_object).config({background: null})
    .data(filtered_students_year).render();  
}


export default function define(runtime, observer) {
  const main = runtime.module();
  function toString() { return this.url; }
  const fileAttachments = new Map([
    ["student_behavior_all_years.csv", {url: new URL("./files/789be6bd870b617b3145ecd1ebef352da682ac15b71481e1ac2e31acef1e0c43fd712ca19a998815de68e06393cfda7ad45c1896c4115d0f1d18061e0471fc97.csv", import.meta.url), mimeType: "text/csv", toString}]
  ]);
  main.builtin("FileAttachment", runtime.fileAttachments(name => fileAttachments.get(name)));
  main.variable(observer()).define(["md"], _1);
  const child1 = runtime.module(define1);
  main.import("vl", child1);
  main.variable(observer("student_behavior_all_years")).define("student_behavior_all_years", ["FileAttachment"], _student_behavior_all_years);
  main.variable(observer("filtered_students_night_sleep")).define("filtered_students_night_sleep", ["student_behavior_all_years"], _filtered_students_night_sleep);
  main.variable(observer("viewof year_drop_down")).define("viewof year_drop_down", ["Inputs"], _year_drop_down);
  main.variable(observer("year_drop_down")).define("year_drop_down", ["Generators", "viewof year_drop_down"], (G, _) => G.input(_));
  main.variable(observer("filtered_students_year")).define("filtered_students_year", ["filtered_students_night_sleep","year_drop_down"], _filtered_students_year);
  main.variable(observer("student_phone")).define("student_phone", ["vl","filtered_students_year"], _student_phone);
  return main;
}
